@media screen and (max-width: 1024.99px) {
    header {
        padding: 34px 0;
        > .container {
            gap: 0;
        }

        &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 97px;
            background-color: $bg-color;
            z-index: 1;
        }
    }

    .container {
        padding: 0 25px;
    }

    .header-logo {
        max-width: 114px;
    }

    .mobile-container {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        background: #080C0D;
        padding: 25px 25px 75px;
        background-image: linear-gradient(245deg, rgba(35, 114, 73, 0.32) 0%, rgba(53, 198, 107, 0.32) 51.85%);
        backdrop-filter: blur(3px);
        transition: .3s;
        visibility: hidden;
        transform: translate(0, -100%);

        &.show {
            top: 97px;
            transform: translate(0, 0);
            visibility: visible;
        }
    }
    

    .menu__list {
        gap: 48px;
        flex-direction: column;
    }

    .btn {
        .btn-outline {
        width: 52px;
        height: 52px;
        border-radius: 18px;
        svg {
            height: 22px;
        }
    }
}

    .banner {
        .container {
            flex-direction: column-reverse;
            align-items: center;
        }

        h1 {
            font-size: min(6vw, 80px);
            font-size: 800;
            margin-bottom: 54px;
            position: relative;
        }
    }

    .footer {
        padding: 37px 0 7px;

        &-logo {
            max-width: 134px;
        }

        &-nav {
            order: 1;
            max-width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            gap: 28px;
            padding: 38px 0 32px;
        }
    
        &-row {
            flex-wrap: wrap;
            margin-bottom: 0;
            .btn {
            margin-left: auto;
        }  
    }

    .footer-nav__title,
    .footer-nav__link {
        font-size: 15px;
    }

    .footer-nav__column {
        padding: 0;
    }

    .footer-phones__item {
        font-size: 16px;
    }

    .footer-social__item {
        height: 34px;
        width: 34px;
    }

    .btn {
        margin-top: 17px;
    }
   
  }
}
