header {
    padding: 48px 0;
    position: relative;

    >.container {
        display: flex;
        align-items: center;
        gap: 94px;
		position: relative;
		&::before {
			@include template-light;
			width: 586px;
			height: 653px;
			background: url(./assets/img/decoration__banner-light-title.png) center no-repeat;
			left: 0;
			top: -150px;
			transform: none;
		}
    }

    .btn-burger {
        margin-left: auto;
    }
}


.header-logo {
	z-index: 1;
}
 

.menu {
	&__list {
		display: flex;
		gap: 94px;
	}
	
	&__link {
		font-size: 20px;
		color: $text;
		text-decoration: none;
  }
}

.btn-burger {
	border: none;
	background: none;
	z-index: 1;
	width: 42px;
	height: 18px;
	position: relative;

	span {
		position: absolute;
		left: 0;
		top: 0;
		display: inline-block;
		height: 2px;
		width: 100%;
		background-color: $secondary-color;
        background-image: linear-gradient(245deg, $primary-color 0%, $secondary-color 100%);
		&:nth-child(2) {
			top: 9px;
		}

		&:last-child {
			top: 18px;
		}
	}
	
	&.active {
		span {
			transform: rotate(45deg);
            top: 8px;
			&:nth-child(2) {
				top: 9px;
				transform: rotate(-45deg);
			}

			&:last-child {
				opacity: 1;
			}
		}
	}
}
